import React from "react";
import HOC from "@/HOC/load-HOC.js";
import { Row, Col } from "antd";
const { AmdDocHOC } = HOC;
import doc from "zerod/scss/doc.md";
export default AmdDocHOC(doc, {
	"text-color": function() {
		const colors = [
			"z-text-blue",
			"z-text-light-blue",
			"z-text-green",
			"z-text-yellow",
			"z-text-red",
			"z-text-black",
			"z-text-light-black",
			"z-text-gray",
			"z-text-light-gray",
		];

		return (
			<div className="z-flex-space-between">
				{colors.map((name, i) => {
					return (
						<div className={name} key={i}>
							{name}
						</div>
					);
				})}
			</div>
		);
	},
	"default-panel": function() {
		return (
			<Row gutter={40}>
				<Col span={12}>
					<div className="z-panel">
						<div className="z-panel-heading">标题</div>
						<div className="z-panel-body">无边框面板</div>
					</div>
				</Col>
				<Col span={12}>
					<div className="z-panel is-panel-border">
						<div className="z-panel-heading">标题</div>
						<div className="z-panel-body">有边框面板</div>
					</div>
				</Col>
			</Row>
		);
	},
	"default-info": function() {
		return (
			<div>
				<dl className="z-info">
					<dt className="z-info-left"><span className="z-margin-bottom-10">标题1</span></dt>
					<dd className="z-info-right">
					<span className="z-margin-bottom-10">为了使我们有更高的可控性，即自由控制顶点位置，WebGL把这个权力交给了我们，这就是可编程渲染管线（不用理解）。</span>
					</dd>
				</dl>
				<dl className="z-info">
					<dt className="z-info-left"><span className="z-margin-bottom-10">标题2</span></dt>
					<dd className="z-info-right"><span className="z-margin-bottom-10">我们引入了一个新的名词，叫“顶点着色器”</span></dd>
				</dl>
			</div>
		);
	},
});
